export type GeoJsonPolygonCoordinates = [number, number][][]

export const isGeoJsonPolygonCoordinates = (object: unknown): object is GeoJsonPolygonCoordinates => {
  try {
    if (!Array.isArray(object) || object.length === 0) throw new Error() // object is not an array of rings
    object.forEach((ring) => {
      if (!Array.isArray(ring) || ring.length < 4) throw new Error() // ring is not an array of enough points
      ring.forEach((lngLat) => {
        if (!Array.isArray(lngLat) || lngLat.length !== 2) throw new Error() // not coordinates
        if (typeof lngLat[0] !== 'number') throw new Error() // not numeric longitude
        if (typeof lngLat[1] !== 'number') throw new Error() // not numeric latitude
      })
      const lastPointIndex = ring.length - 1
      if (ring[0][0] !== ring[lastPointIndex][0] || ring[0][1] !== ring[lastPointIndex][1]) throw new Error() // not a closed ring
    })
  } catch {
    return false
  }

  return true
}
