import gql from 'graphql-tag'
import { PaginatedRecords, ModelResult as GraphModelResult } from '@namcbugdb/common'

import { Api } from '../api'
import { ModelResult } from '../../types'

export const fetchModelResultsBySampleIdQuery = gql`
  query ($sampleId: Int!, $offset: Int) {
    modelResults(sampleIds: [$sampleId], limit: 1000, offset: $offset) {
      records {
        modelId
        modelName
        modelVersion
        modelResult
        condition
        notes
        updatedDate
      }
      nextOffset
    }
  }
`

export const fetchModelResultsBySampleId =
  (api: Api) =>
  async (sampleId: number): Promise<ModelResult[]> => {
    const data = await api.queryAllPages<{
      modelResults: PaginatedRecords<GraphModelResult>
    }>(fetchModelResultsBySampleIdQuery, { sampleId }, { dataProperty: 'modelResults' })

    return data.modelResults.records
  }
