/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import { atom, useRecoilState, useRecoilValue } from 'recoil'
import MuiButton from '@mui/material/Button'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
import log from 'loglevel'

import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import { Sample } from '../../types'
import { useUpdatableState } from '../../lib/useUpdatableState'
import { ReadOnlyField } from '../ReadOnlyField'
import { DateField } from '../DateField'
import { TextField } from '../TextField'
import { NumberField } from '../NumberField'
import {
  lookupListToOptions,
  selectDesignTypes,
  selectEcosystems,
  selectEffortTypes,
  selectHabitats,
  selectMyUser,
  selectSampleMethods,
  selectSitesBySingleSiteId,
  useInvalidate,
} from '../../data/selectors'
import { SitesMapPreview } from '../SitesMap/SitesMapPreview'
import { ConfirmationDialog } from '../ConfirmationDialog'
import { useNotifications } from '../AppNotifications'
import { Divider, Menu } from '../Menu'
import { SamplePredictorValues } from './SamplePredictorValues'
import { SampleModelResults } from './SampleModelResults'
import { WaterbodyNameField } from './WaterbodyNameField'
import { AccordionPanel } from '../AccordionPanel'
import { Title } from '../Title'
import { formatUtcDateTimeToAppDate } from '../../lib/formats'
import { AutocompleteField } from '../AutocompleteField'
import { deleteSample, updateProject, updateSample } from '../../data/calls'
import { useApi } from '../../data/api'
import { round } from 'lodash'
import { SiteSelectField } from './SiteSelectField'

export interface SampleDetailProps {
  sample: Sample
}

const FIELD_SPLIT_PERCENT_PRECISION = 3

type Fields = Pick<
  Sample,
  | 'area'
  | 'customerSiteCode'
  | 'designTypeId'
  | 'ecosystemId'
  | 'effortTypeId'
  | 'fieldNotes'
  | 'habitatId'
  | 'jarCount'
  | 'labNotes'
  | 'mesh'
  | 'sampleDate'
  | 'sampleLatitude'
  | 'sampleLongitude'
  | 'sampleMethodId'
  | 'siteId'
  | 'visitId'
> & { fieldSplitPercent: number }

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping' | 'deleting'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const getInitialState = ({ sample }: Pick<SampleDetailProps, 'sample'>): State => {
  const {
    area,
    customerSiteCode,
    designTypeId,
    ecosystemId,
    effortTypeId,
    fieldNotes,
    fieldSplit,
    habitatId,
    jarCount,
    labNotes,
    mesh,
    sampleDate,
    sampleLatitude,
    sampleLongitude,
    sampleMethodId,
    siteId,
    visitId,
  } = sample
  return {
    editMode: 'stopped',
    isDirty: false,
    values: {
      area,
      customerSiteCode,
      designTypeId,
      ecosystemId,
      effortTypeId,
      fieldNotes,
      fieldSplitPercent: fieldSplit && round(fieldSplit * 100, FIELD_SPLIT_PERCENT_PRECISION),
      habitatId,
      jarCount,
      labNotes,
      mesh,
      sampleDate,
      sampleLatitude,
      sampleLongitude,
      sampleMethodId,
      siteId,
      visitId,
    },
  }
}

const isExpandedState = atom<boolean>({
  key: 'SampleDetail/isExpandedState',
  default: true,
})

export const SampleDetail = ({ sample }: SampleDetailProps): JSX.Element => {
  const navigate = useNavigate()
  const api = useApi()
  const invalidate = useInvalidate()
  const notifications = useNotifications()
  const user = useRecoilValue(selectMyUser)

  // LOOKUP OPTIONS

  const sampleMethodOptions = lookupListToOptions(useRecoilValue(selectSampleMethods))
  const habitatOptions = lookupListToOptions(useRecoilValue(selectHabitats))
  const ecosystemOptions = lookupListToOptions(useRecoilValue(selectEcosystems))
  const effortTypeOptions = lookupListToOptions(useRecoilValue(selectEffortTypes))
  const designTypeOptions = lookupListToOptions(useRecoilValue(selectDesignTypes))

  // READ-ONLY FIELDS

  const {
    boxId, //
    boxState,
    customerName,
    id,
    measurements,
    sampleTypeName,
    siteName,
    submittedByName,
    usState,
  } = sample

  // STATE

  const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ sample }), [sample])
  const [isPanelExpanded, setIsPanelExpanded] = useRecoilState(isExpandedState)
  const [isConfirmDeleteOpen, setIsConfirmDeleteOpen] = useState(false)

  const {
    values: {
      area,
      customerSiteCode,
      designTypeId,
      ecosystemId,
      effortTypeId,
      fieldNotes,
      fieldSplitPercent,
      habitatId,
      jarCount,
      labNotes,
      mesh,
      sampleDate,
      sampleLatitude,
      sampleLongitude,
      sampleMethodId,
      siteId,
      visitId,
    },
    editMode,
    isDirty,
  } = state

  const errors: FieldErrors = (() => {
    const result: FieldErrors = {
      jarCount: !jarCount,
      sampleLatitude: sampleLatitude === null && sampleLongitude !== null,
      sampleLongitude: sampleLongitude === null && sampleLatitude !== null,
      // TODO: what other field limits or errors?
    }
    if (!Object.values(result).some((value) => value)) return undefined
    return result
  })()

  const isStopped = editMode === 'stopped'
  const isEditing = editMode === 'started'
  const isSaving = editMode === 'stopping'
  const isDeleting = editMode === 'deleting'

  // HANDLERS

  const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
    updateState((draft) => {
      Object.assign(draft.values, { [fieldName]: value })
      draft.isDirty = true
    })
  }
  const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

  const handleEdit = () => {
    setIsPanelExpanded(true)
    updateState((draft) => {
      draft.editMode = 'started'
      draft.lastValues = draft.values
    })
  }

  const handleCancel = () => {
    updateState((draft) => {
      if (!draft.lastValues) throw new Error('no values to revert to')
      draft.editMode = 'stopped'
      draft.values = draft.lastValues
      draft.isDirty = false
    })
  }

  const handleSave = async () => {
    try {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      const sampleInput = {
        area,
        customerSiteCode: customerSiteCode.trim(),
        designTypeId,
        ecosystemId,
        effortTypeId,
        fieldNotes: fieldNotes.trim(),
        fieldSplit: fieldSplitPercent / 100,
        habitatId,
        jarCount,
        labNotes: labNotes.trim(),
        mesh,
        sampleDate,
        sampleLatitude,
        sampleLongitude,
        sampleMethodId,
        siteId,
        visitId: visitId.trim(),
      }
      await updateSample(
        api,
        invalidate
      )({
        ...sampleInput,
        id: sample.id,
      })
      updateState((draft) => {
        draft.editMode = 'stopped'
        draft.isDirty = false
      })
    } catch (err) {
      notifications.error(`Failed to save: ${err}`)
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }
  }

  const handleDelete = async () => {
    try {
      if (!sample) throw new Error('No sample.')
      updateState((draft) => {
        draft.editMode = 'deleting'
      })
      await deleteSample(api, invalidate)(sample)
      notifications.info(`Deleted sample ${id}`)
      navigate.boxDetail({ boxId })
    } catch (err) {
      notifications.error(`Failed to delete: ${err}`)
      updateState((draft) => {
        draft.editMode = 'stopped'
      })
    }
  }

  // RENDER

  const { sortEndDate, sortStartDate, sorterName } = sample
  const sortState = (() => {
    if (sortEndDate) return `${sorterName} on ${formatUtcDateTimeToAppDate(sortEndDate)}`
    if (!sortStartDate) return 'Not sorted'
    return `Started by ${sorterName} on ${formatUtcDateTimeToAppDate(sortStartDate)}`
  })()

  const { identifierName, identificationEndDate, identificationStartDate } = sample
  const identificationState = (() => {
    if (identificationEndDate)
      return `${identifierName ?? 'Unknown'} on ${formatUtcDateTimeToAppDate(identificationEndDate)}`
    if (!identificationStartDate) return 'Not identified'
    return `Started by ${identifierName} on ${formatUtcDateTimeToAppDate(identificationStartDate)}`
  })()

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <AccordionPanel
          value={isPanelExpanded}
          onChange={setIsPanelExpanded}
          locked={isEditing}
          summary={
            <Title>
              Sample {id} <Title.Subtitle>(Box {boxId})</Title.Subtitle>
            </Title>
          }
          actions={
            <div css={{ display: 'flex', gap: '0.5em' }}>
              {isStopped && user.hasPermission(NAMCResource.samples, GrantAction.updateAny) && (
                <Menu
                  label="Actions"
                  disabled={isDeleting}
                  items={[
                    {
                      key: 'edit',
                      label: 'Edit Sample',
                      onClick: handleEdit,
                    },
                    Divider,
                    {
                      key: 'delete',
                      label: 'Delete Sample',
                      onClick: () => {
                        setIsConfirmDeleteOpen(true)
                      },
                    },
                  ]}
                />
              )}
              {(isEditing || isSaving) && (
                <>
                  <MuiButton onClick={handleCancel} variant="outlined" disabled={isSaving}>
                    Cancel
                  </MuiButton>
                  <MuiButton onClick={handleSave} variant="contained" disabled={!isDirty || isSaving || !!errors}>
                    Save
                  </MuiButton>
                </>
              )}
            </div>
          }
        >
          <div
            css={{
              // flex: 1,
              display: 'grid',
              gap: '0.75em',
              gridTemplateColumns: 'repeat(4, auto)',
            }}
          >
            <div
              css={{
                // gridArea: 'map',
                minHeight: '300px',
                gridColumn: '4',
                gridRow: '1 / 6',
              }}
            >
              <SitesMapPreview sitesListingSource={null} sitesSelector={selectSitesBySingleSiteId(siteId)} />
            </div>
            <ReadOnlyField
              // gridArea="sampleTypeName"
              label="Sample Type"
              value={sampleTypeName}
            />
            <ReadOnlyField
              // gridArea="boxId"
              label="Box ID"
              value={
                <a
                  onClick={() => {
                    navigate.boxDetail({ boxId })
                  }}
                >
                  {boxId}
                </a>
              }
            />
            <SiteSelectField
              //  gridArea="site"
              label="Site"
              value={siteId}
              onChange={makeFieldChangeHandler('siteId')}
              valueLabel={siteName}
              readOnly={!isEditing}
            />
            <ReadOnlyField
              // gridArea="usState"
              label="US State"
              value={usState}
            />
            <ReadOnlyField
              // gridArea="customerName"
              label="Customer"
              value={customerName}
            />
            <ReadOnlyField
              // gridArea="submittedByName"
              label="Submitted By"
              value={submittedByName}
            />
            <ReadOnlyField
              // gridArea="boxState"
              label="Box State"
              value={boxState}
            />
            <ReadOnlyField
              // gridArea="measurements"
              label="Measurements"
              value={measurements}
            />
            <TextField
              // gridArea="visitId"
              label="Visit ID"
              value={visitId}
              onChange={makeFieldChangeHandler('visitId')}
              readOnly={!isEditing}
              error={errors?.visitId}
            />
            <DateField
              // gridArea="sampleDate"
              label="Sample Date"
              value={sampleDate}
              onChange={makeFieldChangeHandler('sampleDate')}
              readOnly={!isEditing}
              error={errors?.sampleDate}
            />
            <AutocompleteField
              // gridArea="sampleMethodId"
              label="Sample Method"
              value={sampleMethodId}
              onChange={makeFieldChangeHandler('sampleMethodId')}
              readOnly={!isEditing}
              error={errors?.sampleMethodId}
              options={sampleMethodOptions}
            />
            <AutocompleteField
              // gridArea="habitatId"
              label="Habitat"
              value={habitatId}
              onChange={makeFieldChangeHandler('habitatId')}
              readOnly={!isEditing}
              options={habitatOptions}
            />
            <AutocompleteField
              // gridArea="ecosystemId"
              label="Ecosystem"
              value={ecosystemId}
              onChange={makeFieldChangeHandler('ecosystemId')}
              readOnly={!isEditing}
              options={ecosystemOptions}
              isClearable
            />
            <TextField
              // gridArea="customerSiteCode"
              label="Customer Site Code"
              value={customerSiteCode}
              onChange={makeFieldChangeHandler('customerSiteCode')}
              readOnly={!isEditing}
              error={errors?.customerSiteCode}
            />
            <NumberField
              // gridArea="sampleLatitude"
              label="Sampled Latitude"
              value={sampleLatitude}
              onChange={makeFieldChangeHandler('sampleLatitude')}
              readOnly={!isEditing}
              error={errors?.sampleLatitude}
              min={-90}
              max={90}
            />
            <NumberField
              // gridArea="sampleLongitude"
              label="Sampled Longitude"
              value={sampleLongitude}
              onChange={makeFieldChangeHandler('sampleLongitude')}
              readOnly={!isEditing}
              error={errors?.sampleLongitude}
              min={-180}
              max={180}
            />
            <WaterbodyNameField
              //  gridArea="waterbodyName"
              label="Waterbody Name"
              siteId={siteId}
            />
            <NumberField
              // gridArea="area"
              label="Area Sampled (m²)"
              value={area}
              onChange={makeFieldChangeHandler('area')}
              readOnly={!isEditing}
              error={errors?.area}
              min={0}
              max={5}
            />
            <NumberField
              // gridArea="fieldSplitPercent"
              label="Field Split %"
              value={fieldSplitPercent}
              onChange={makeFieldChangeHandler('fieldSplitPercent')}
              readOnly={!isEditing}
              error={errors?.fieldSplitPercent}
              min={0}
              max={100}
              precision={FIELD_SPLIT_PERCENT_PRECISION}
            />
            <TextField
              // gridArea="fieldNotes"
              label="Field Notes"
              value={fieldNotes}
              onChange={makeFieldChangeHandler('fieldNotes')}
              readOnly={!isEditing}
              error={errors?.fieldNotes}
            />
            <NumberField
              // gridArea="jarCount"
              label="Jar Count"
              value={jarCount}
              onChange={makeFieldChangeHandler('jarCount')}
              readOnly={!isEditing}
              error={errors?.jarCount}
              min={1}
              precision={0}
            />
            <NumberField
              // gridArea="mesh"
              label="Mesh Size (μm)"
              value={mesh}
              onChange={makeFieldChangeHandler('mesh')}
              readOnly={!isEditing}
              error={errors?.mesh}
            />
            <AutocompleteField
              // gridArea="effortTypeId"
              label="Effort Type"
              value={effortTypeId}
              onChange={makeFieldChangeHandler('effortTypeId')}
              readOnly={!isEditing}
              options={effortTypeOptions}
              isClearable
            />
            <AutocompleteField
              // gridArea="designTypeId"
              label="Design Type"
              value={designTypeId}
              onChange={makeFieldChangeHandler('designTypeId')}
              readOnly={!isEditing}
              options={designTypeOptions}
              isClearable
            />
            <TextField
              // gridArea="labNotes"
              label="Lab Notes"
              value={labNotes}
              onChange={makeFieldChangeHandler('labNotes')}
              readOnly={!isEditing}
              error={errors?.labNotes}
            />
            <ReadOnlyField
              // gridArea=""
              label="Sorting"
              value={
                <a
                  onClick={() => {
                    navigate.sampleSorting({ sampleId: sample.id })
                  }}
                >
                  {sortState}
                </a>
              }
            />
            <ReadOnlyField
              // gridArea=""
              label="Identification"
              value={
                <a
                  onClick={() => {
                    navigate.sampleTaxonomyIdentification({ sampleId: sample.id })
                  }}
                >
                  {identificationState}
                </a>
              }
            />
          </div>
        </AccordionPanel>
        <SamplePredictorValues sampleId={sample.id} />
        <div css={{ borderBottom: '1px solid lightgrey', height: '1px', margin: '1rem 0' }} />
        <SampleModelResults sampleId={sample.id} />
      </div>
      <ConfirmationDialog
        title="Confirm Delete Sample"
        isOpen={isConfirmDeleteOpen}
        onClose={() => {
          setIsConfirmDeleteOpen(false)
        }}
        onConfirm={handleDelete}
      >
        Are you sure you want to delete this sample?
      </ConfirmationDialog>
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
